@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: #181818;

    @include m.tablet{
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            z-index: 999;
            animation: slideInDown .15s linear both;
        }
    }

    h1{
        color: v.$blanco;
        // font-family: 'Lobster';       
        font-family: 'Cafenero',Helvetica,Arial,Lucida,sans-serif;
         // text-align: start;
        // font-size: 2.4rem;
        margin-top: 0;
        @include m.tablet {
            margin: 0;
        }
    }
}

