.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion .menu-horizontal {
  list-style: none;
  padding: 0;
}
@media (min-width: 768px) {
  .navegacion .menu-horizontal {
    display: flex;
    justify-content: space-around;
  }
}
@media (min-width: 768px) {
  .navegacion .menu-horizontal > li {
    margin-right: 2rem;
  }
  .navegacion .menu-horizontal > li:last-of-type {
    margin-right: 0;
  }
}
.navegacion .menu-horizontal > li a {
  display: block;
  font-size: 2.2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion .menu-horizontal > li a {
    font-size: 1.8rem;
  }
}
.navegacion .menu-horizontal .menu-vertical {
  position: absolute;
  visibility: hidden;
  list-style: none;
  width: 11rem;
  padding: 16px 0 16px 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  -webkit-transition: all 0.3s 0.1s;
  -moz-transition: all 0.3s 0.1s;
  -o-transition: all 0.3s 0.1s;
  transition: all 0.3s 0.1s;
  border-radius: 5px;
  -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.4);
  z-index: 1;
}
.navegacion .menu-horizontal .menu-vertical li {
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion .menu-horizontal li:hover ul {
    visibility: visible;
    opacity: 1;
  }
}

