.mobile-menu {
  color: #FFFFFF;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu span {
  display: block;
  width: 100%;
  height: 2px;
  background: #FFFFFF;
  margin-top: 6px;
  margin-left: 6px;
  transform-origin: 0px 100%;
  transition: all 300ms;
}
.mobile-menu .activeline1__mobile-menu {
  transform: rotate(45deg) translate(-2px, 1px);
}
.mobile-menu .activeline2__mobile-menu {
  opacity: 0;
  margin-left: -30px;
}
.mobile-menu .activeline3__mobile-menu {
  transform: rotate(-45deg) translate(-4px, 2px);
}

