@use 'base/mixins' as m;
@use 'base/variables' as v;

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
    }
}

.navegacion{
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear; 

    @include m.tablet{
        display: flex;
        align-items: center;
        visibility: visible;
        opacity: 1;
    }
    .menu-horizontal{
        list-style: none;
        padding: 0;
        @include m.tablet{
            display: flex;
            justify-content: space-around;
        }

        &>li{
            @include m.tablet {
                margin-right: 2rem;
                &:last-of-type {
                    margin-right: 0;
                }
            }

            a{
                display: block;
                font-size: 2.2rem;
                text-align: center;
                @include m.tablet {
                    font-size: 1.8rem;                    
                }
            }    
        }
            
        .menu-vertical{    

            position: absolute;
            visibility: hidden;
            list-style: none;
            width: 11rem;
            padding: 16px 0 16px 0;
            background-color: rgba(0, 0, 0, .5);      
            opacity: 0;
            -webkit-transition: all .3s .1s;
            -moz-transition: all .3s .1s;
            -o-transition: all .3s .1s;
            transition: all .3s .1s;
            border-radius: 5px;
            -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.4);
            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.4);
            z-index: 1;
            
            // // margin-left: 1rem;
            // background-color: rgba(0, 0, 0, .5);
            // // padding: 0;

            li{
                text-align: center;
                // padding: 0px 16px 0px 16px;
            }

        }

        & li:hover ul {    
            @include m.tablet{
                // display: block;
                visibility: visible;
                opacity: 1;
                // top: 42px;
            }
        }
    }
}        

/************ Menu hamburguesa /************/
.mobile-menu{
    color: v.$blanco;
    width: 30px;
    height: 30px;
    cursor: pointer;
    @include m.tablet{
        display: none;
    }

    span{
        display: block;
        width: 100%;
        height: 2px;
        background: v.$blanco;
        margin-top: 6px;
        margin-left: 6px;
        transform-origin: 0px 100%;
        transition: all 300ms;
    }

    .activeline1__mobile-menu{
        transform: rotate(45deg) translate(-2px, 1px); 
    }
    
    .activeline2__mobile-menu{
        opacity: 0;
        margin-left: -30px; 
    }
    
    .activeline3__mobile-menu{
        transform: rotate(-45deg) translate(-4px, 2px);
    }
}

.mostar {
    visibility: visible;
    opacity: 1;
    height: auto;
    transition: all 600ms;
    // transition: visibility 600ms ease-in-out, opacity 600ms linear, height .5s linear; 
}